.container {
    padding: 20px;
}

.input-group {
    margin-bottom: 20px;
}

.spell-item {
    margin-bottom: 10px;
}

.pressable {
    cursor: pointer;
    color: #008080;
    text-decoration: underline;
}

.pressable:hover {
    color: #026b6b;
}

.description {
    text-align: left; /* Align text to the left */
    margin-left: 20px; /* Add some left margin for better visibility */
    white-space: pre-line; /* Preserve whitespace formatting */
    font-size: 0.9em; /* Slightly smaller font size for descriptions */
}

/* Responsive design for smaller screens */
@media (max-width: 768px) {
    .container {
        padding: 10px;
    }

    .input-group {
        margin-bottom: 15px;
        display: flex;
        flex-direction: column;
    }

    .description {
        margin-left: 10px; /* Less left margin for smaller screens */
        font-size: 1em; /* Slightly larger font size for better readability on smaller screens */
    }

    h2, h3, h4 {
        font-size: 1.2em; /* Smaller headings for mobile */
    }

    label {
        font-size: 1em; /* Slightly smaller labels for mobile */
    }

    /* Ensuring buttons are displayed vertically */
    .button-group {
        display: flex;
        flex-direction: column;
    }

    .button-group button {
        margin-bottom: 10px;
    }
}

/* Further adjustments for very small screens */
@media (max-width: 480px) {
    .container {
        padding: 5px;
    }

    .input-group {
        margin-bottom: 10px;
        display: flex;
        flex-direction: column;
    }

    .description {
        margin-left: 5px; /* Even less left margin for very small screens */
        font-size: 1.1em; /* Increase font size for better readability on very small screens */
    }

    h2, h3, h4 {
        font-size: 1em; /* Smaller headings for very small screens */
    }

    label {
        font-size: 0.9em; /* Smaller labels for very small screens */
    }

    /* Ensuring buttons are displayed vertically */
    .button-group {
        display: flex;
        flex-direction: column;
    }

    .button-group button {
        margin-bottom: 10px;
    }
}
